.present {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 80px;
  row-gap: 20px;
}

.present span {
  display: block;
  text-align: center;
}

.present_first_title {
  margin: auto 200px;
  text-align: center;
}

.present_para {
  margin: 20px 150px 0;
}

.present_second_title {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.present_img {
  display: flex;
  margin: 10px -20px;
  border-top: 2px solid #fff;
  border-bottom: 2px solid #fff;
}

.present_img img {
  width: 100%;
  height: 350px;
}
